@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-bold.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PTMono.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: "PT Mono", "Courier New", monospace;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
@media (max-width: 767px) {
  html {
    font-size: 14px;
    line-height: 19px;
  }
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #f2f5f7;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #041827;
}

input,
textarea {
  border-radius: 3px;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
}

html {
  scroll-behavior: smooth;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.reset-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

@media (max-width: 1023px) {
  .hidden-tablet {
    display: none;
  }
}

@media (min-width: 1024px) {
  .hidden-desktop {
    display: none;
  }
}

@media (max-width: 767px) {
  .hidden-mobile {
    display: none;
  }
}

@media (min-width: 768px) {
  .hidden-not-mobile {
    display: none;
  }
}

[data-hide]:not(.is-open) div {
  display: none;
}

@media (max-width: 767px) {
  [data-accordion-item] {
    position: relative;
  }
  [data-accordion-item].is-open [data-accordion-button]::after {
    display: none;
  }
  [data-accordion-item]:not(.is-open) [data-accordion-content] {
    display: none;
  }
}
.input-message {
  padding-left: 4px;
  font-size: 10px;
  line-height: 1;
}

.container {
  width: 100%;
  max-width: 1302px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.button {
  display: inline-flex;
  justify-content: center;
  text-align: center;
  padding: 11px 16px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: inherit;
  background-color: transparent;
  border: 1px solid;
  border-radius: 3px;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .button:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.button.focus-visible:focus {
  opacity: 0.8;
}

.title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 51px;
}
@media (max-width: 767px) {
  .title {
    font-size: 28px;
    line-height: 39px;
  }
}

.header {
  padding: 30px 0 35px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  color: #f2f5f7;
  z-index: 2;
  position: fixed;
  top: 0;
  width: 100%;
}
@media (max-width: 1023px) {
  .header {
    position: static;
    padding: 20px 0 25px;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 10px 0 20px;
  }
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-right: 102px;
}
.header__logo {
  min-width: 230px;
}
@media (max-width: 767px) {
  .header__logo {
    padding-top: 5px;
    min-width: 55px;
  }
  .header__logo img {
    width: 35px;
  }
}
.header__button {
  margin-top: 5px;
  border-color: #f2f5f7;
  min-width: 160px;
}
.header__button:active {
  color: #f2f5f7;
  background-color: #00b2ff;
  border-color: #00b2ff;
}

.nav__list {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  padding-top: 5px;
}
@media (max-width: 1023px) {
  .nav__list {
    gap: 20px;
    margin-right: 20px;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .nav__list {
    margin-right: 10px;
  }
}
.nav__item {
  width: 225px;
}
@media (max-width: 1023px) {
  .nav__item {
    width: 40px;
  }
}
.nav__link {
  position: relative;
  display: flex;
  gap: 15px;
  padding: 10px 0;
  line-height: 18px;
}
.nav__link:hover, .nav__link:focus {
  color: #00b2ff;
}
.nav__link:active {
  color: #004968;
}
@media (max-width: 1023px) {
  .nav__link {
    gap: 0;
    padding: 10px;
  }
  .nav__link:focus {
    background-color: #f2f5f7;
  }
  .nav__link:active {
    background-color: #004968;
  }
}

.homescreen {
  background: #041827 url("./../img/homescreen-desktop@1x.jpg") no-repeat center/cover;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .homescreen {
    background-image: url("./../img/homescreen-desktop@2x.jpg");
  }
}
.homescreen__container {
  display: flex;
  flex-direction: column;
  gap: 40px;
  align-items: flex-start;
  margin-bottom: 120px;
  padding: 305px 125px 280px 166px;
  color: #f2f5f7;
  z-index: 0;
}
@media (max-width: 1023px) {
  .homescreen__container {
    gap: 10px;
    margin-top: -100px;
    margin-bottom: 80px;
    padding-top: 180px;
    padding-bottom: 205px;
  }
}
@media (max-width: 767px) {
  .homescreen__container {
    margin-top: -80px;
    margin-bottom: 50px;
    padding-top: 160px;
    padding-bottom: 192px;
    gap: 15px;
  }
}
.homescreen h2 {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .homescreen h2 {
    font-size: 64px;
    line-height: 75px;
  }
}
@media (max-width: 767px) {
  .homescreen h2 {
    font-size: 36px;
    line-height: 42px;
  }
}
.homescreen p {
  margin: 0;
  margin-top: -8px;
  max-width: 685px;
  font-size: 22px;
  line-height: 34px;
}
@media (max-width: 1023px) {
  .homescreen p {
    margin-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .homescreen p {
    font-size: 14px;
    line-height: 22px;
    max-width: 320px;
    margin-bottom: 30px;
  }
}
.homescreen__button {
  margin-top: 5px;
  padding: 16px 26px;
  font-size: 18px;
  line-height: 21px;
  background: #00b2ff;
  border-color: #00b2ff;
}
.homescreen__button:active {
  color: #00b2ff;
  background-color: transparent;
}
@media (max-width: 1023px) {
  .homescreen__button {
    margin-left: 45px;
  }
}
@media (max-width: 767px) {
  .homescreen__button {
    font-size: 14px;
    line-height: 16px;
    align-self: stretch;
    max-width: 320px;
    margin: 0;
    padding: 10px;
  }
}

.features__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin-bottom: 120px;
}
@media (max-width: 1023px) {
  .features__list {
    margin-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .features__list {
    grid-template-columns: 1fr;
    margin-bottom: 50px;
  }
}
.features__item {
  min-height: 300px;
  padding: 40px 45px 25px 41px;
  color: #00b2ff;
  border: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
}
.features__item:hover {
  color: #f2f5f7;
  background-color: #00b2ff;
}
.features__item:hover p {
  color: #f2f5f7;
}
@media (max-width: 1023px) {
  .features__item {
    min-height: 360px;
    padding-right: 30px;
    padding-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .features__item {
    padding: 30px 25px 25px;
  }
  .features__item:nth-of-type(n), .features__item:nth-of-type(2n) {
    min-height: 340px;
  }
  .features__item:nth-of-type(3n), .features__item:nth-of-type(4n) {
    min-height: 325px;
  }
  .features__item:nth-of-type(3n) h2, .features__item:nth-of-type(4n) h2 {
    margin-bottom: 15px;
  }
  .features__item svg {
    width: 30px;
    height: 30px;
  }
}
.features h2 {
  margin: 15px 0 12px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 34px;
}
@media (max-width: 1023px) {
  .features h2 {
    margin: 11px 0 5px;
  }
}
@media (max-width: 767px) {
  .features h2 {
    font-size: 20px;
    line-height: 28px;
    margin: 10px 0;
  }
}
.features p {
  margin: 0;
  margin-bottom: 10px;
  color: #041827;
}

.about__container {
  display: grid;
  grid-template-columns: 150px 1fr;
  gap: 100px;
  margin-bottom: 101px;
  min-height: 320px;
}
@media (max-width: 1023px) {
  .about__container {
    gap: 30px;
    margin-bottom: 78px;
    padding: 0 46px;
  }
}
@media (max-width: 767px) {
  .about__container {
    grid-template-columns: 1fr;
    margin-bottom: 65px;
  }
}
.about__date {
  background: #ffffff url("./../img/company-date.png") no-repeat top/150px 320px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .about__date {
    background-image: url("./../img/company-date@2x.png");
  }
}
.about h2 {
  margin: 0;
  margin-bottom: 13px;
}
@media (max-width: 1023px) {
  .about h2 {
    margin-top: -4px;
    margin-bottom: 9px;
  }
}
@media (max-width: 767px) {
  .about h2 {
    margin-bottom: 12px;
  }
}
.about p {
  margin: 0;
  margin-bottom: 20px;
}
@media (max-width: 1023px) {
  .about p:last-of-type {
    margin-bottom: 18px;
  }
}
@media (max-width: 767px) {
  .about p:last-of-type {
    margin-bottom: 13px;
  }
}
.about__button {
  width: 180px;
  margin-top: 20px;
  padding-top: 14px;
  padding-bottom: 13px;
  color: #00b2ff;
}
.about__button:hover, .about__button:focus {
  color: #004968;
  border-color: #004968;
}
.about__button:active {
  color: #ffffff;
  background-color: #00b2ff;
}
@media (max-width: 1023px) {
  .about__button {
    margin: 0;
  }
}
@media (max-width: 767px) {
  .about__button {
    width: 160px;
    padding: 8px;
  }
}

.product {
  background-color: #041827;
}
.product__container {
  padding-top: 66px;
  padding-bottom: 140px;
}
@media (max-width: 1023px) {
  .product__container {
    padding-top: 56px;
    padding-bottom: 75px;
  }
}
@media (max-width: 767px) {
  .product__container {
    padding-top: 47px;
    padding-bottom: 86px;
  }
}
.product h2 {
  margin: 0;
  margin-bottom: 54px;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .product h2 {
    margin-bottom: 37px;
  }
}
@media (max-width: 767px) {
  .product h2 {
    margin-bottom: 29px;
  }
}
.product__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 85px;
}
@media (max-width: 1023px) {
  .product__list {
    grid-template-columns: 1fr;
    gap: 30px;
  }
}
.product__link {
  position: relative;
  display: block;
  width: 100%;
  height: 650px;
  color: #ffffff;
  background-color: #004968;
}
@media (hover: hover) {
  .product__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.product__link.focus-visible:focus {
  opacity: 0.8;
}
.product__link:active .product__button {
  color: #00b2ff;
